import React, { useState, useEffect } from "react";
import { MDBInput, MDBBtn } from "mdbreact";
import { useHistory } from "react-router-dom";
import Swal from "sweetalert2";
import profileImg from "../img/profile/profile.png";
import axios from "axios";
import Loader from "react-loader-spinner";
import API_URL from "./api_constants/apiUrl";
import { HEADERS_WITH_TOKEN } from "./api_constants/headers";
import tna from "../img/TNA.png";

const UserProfile = () => {
  const history = useHistory();

  const headers = HEADERS_WITH_TOKEN;

  const [userInfo, setUserInfo] = useState({
    id: "",
    first_name: "",
    last_name: "",
    email: "",
    mobile_phone: "",
    birth_date: "",
  });
  const [loading, setLoading] = useState(false);
  const [updating, setUpdating] = useState(false);

  useEffect(() => {
    getUserInfo();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  const getUserInfo = async () => {
    setLoading(true);
    try {
      const res = await axios.get(`${API_URL}/patients/profile/`, { headers });
      renderNewUserInformation(res.data);
      setLoading(false);
    } catch (e) {
      const error = e.response.data;
      const message =
        [Object.keys(error)[0]] + ": " + error[Object.keys(error)[0]];
      errorPopup(message);
      setLoading(false);
    }
  };

  const updateUserInformation = async () => {
    setUpdating(true);
    try {
      const res = await axios.put(
        `${API_URL}/patients/profile/`,
        { ...userInfo },
        { headers }
      );
      renderNewUserInformation(res.data);
      setUpdating(false);
    } catch (e) {
      const error = e.response.data;
      const message =
        [Object.keys(error)[0]] + ": " + error[Object.keys(error)[0]];
      errorPopup(message);
      setUpdating(false);
    }
  };

  const renderNewUserInformation = (userData) => {
    Object.keys(userData).forEach((key) => {
      setUserInfo({
        ...userData,
        key: userData[key],
      });
    });
  };

  const handleInput = (e) => {
    setUserInfo({
      ...userInfo,
      [e.target.name]: e.target.value,
    });
  };

  const errorPopup = (message) => {
    Swal.fire({
      icon: "error",
      title: "Oops!...",
      text: message,
    });
  };

  return (
    <>
      {loading ? (
        <Loader
          style={{ textAlign: "center" }}
          type="MutatingDots"
          color="white"
          height={100}
          width={100}
        />
      ) : (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            position: "relative",
          }}
        >
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              marginBottom: "16px",
              color: "white",
            }}
          >
            <img
              style={{ marginBottom: "16px", alignSelf: "center" }}
              src={tna}
              alt="TNA"
              onClick={() => history.push("/menu")}
            />
            <i
              onClick={() => history.push("/menu")}
              className="fa fa-chevron-left"
              aria-hidden="true"
              style={{
                position: "absolute",
                fontSize: "18px",
                top: "15px",
              }}
            ></i>
            <div
              style={{
                alignSelf: "flex-end",
                fontSize: "20px",
                alignItems: "center",
                display: "flex",
              }}
            >
              <span>Mis datos</span>
              <img
                style={{
                  width: "50px",
                  borderRadius: "50%",
                  marginLeft: "10px",
                }}
                src={profileImg}
                alt="profile"
              ></img>
            </div>
          </div>
          <form style={{ width: "100%" }}>
            <div className="grey-text">
              <MDBInput
                style={{ color: "white" }}
                label="Nombre"
                icon="file-signature"
                group
                type="text"
                validate
                error="wrong"
                success="right"
                name="first_name"
                value={userInfo.first_name}
                onInput={handleInput}
              />
              <MDBInput
                style={{ color: "white" }}
                label="Apellido"
                icon="file-signature"
                group
                type="text"
                validate
                error="wrong"
                success="right"
                name="last_name"
                value={userInfo.last_name}
                onInput={handleInput}
              />
              <MDBInput
                style={{ color: "white" }}
                label="Email"
                icon="envelope"
                group
                type="email"
                validate
                error="wrong"
                success="right"
                name="email"
                value={userInfo.email}
                onInput={handleInput}
              />
              <MDBInput
                style={{ color: "white" }}
                label="Celular"
                icon="mobile-alt"
                group
                type="number"
                validate
                error="wrong"
                success="right"
                name="mobile_phone"
                value={userInfo.mobile_phone}
                onInput={handleInput}
              />
              <MDBInput
                style={{ color: "white" }}
                label="Fecha de nacimiento"
                icon="file-signature"
                group
                type="text"
                validate
                error="wrong"
                success="right"
                name="birth_date"
                value={userInfo.birth_date}
                onInput={handleInput}
              />
            </div>
            <div className="text-center py-2 mt-3">
              <MDBBtn
                color="success"
                onClick={!updating ? updateUserInformation : null}
              >
                {updating ? (
                  <Loader
                    type="ThreeDots"
                    color="white"
                    height={16}
                    width={75}
                  />
                ) : (
                  "Guardar cambios"
                )}
              </MDBBtn>
            </div>
          </form>
        </div>
      )}
    </>
  );
};

export default UserProfile;
