import React, { useEffect, useState } from "react";
import axios from "axios";
import Swal from "sweetalert2";
import workout from "../img/home/workout.jpg";
import workout2 from "../img/sessions/workout2.jpg";
import workout3 from "../img/sessions/workout3.jpg";
import workout4 from "../img/sessions/workout4.jpg";
import workout5 from "../img/sessions/workout5.jpg";
import workout6 from "../img/sessions/workout6.jpg";
import workout7 from "../img/sessions/workout7.jpg";
import workout8 from "../img/sessions/workout8.jpg";
import { useHistory } from "react-router-dom";
import Loader from "react-loader-spinner";
import "../styles/session.scss";
import API_URL from "./api_constants/apiUrl";
import { HEADERS_WITH_TOKEN } from "./api_constants/headers";
import tna from "../img/TNA.png";

const Sessions = () => {
  const history = useHistory();
  const [loading, setLoading] = useState(false);
  const [sessions, setSessions] = useState([]);

  const headers = HEADERS_WITH_TOKEN;

  const getUserRoutine = async () => {
    setLoading(true);
    try {
      const result = await axios.get(`${API_URL}/training/routines/`, {
        headers,
      });
      setLoading(false);
      setSessions(result.data);
      console.log("sessions", result.data);
    } catch (e) {
      const error = e.response.data;
      const message = error[Object.keys(error)[0]];
      errorPopup(message);
      setLoading(false);
    }
  };

  const errorPopup = (message) => {
    Swal.fire({
      icon: "error",
      title: "Oops!...",
      text: message,
    });
  };

  useEffect(() => {
    getUserRoutine();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  const onSessionClick = (id) => {
    localStorage.setItem("session_id", id);
    history.push("/ejercicios");
  };

  const getSessionImage = (index) => {
    switch (index) {
      case 1:
        return workout8;
      case 2:
        return workout2;
      case 3:
        return workout3;
      case 4:
        return workout5;
      case 5:
        return workout4;
      case 6:
        return workout6;
      case 7:
        return workout7;
      default:
        return workout;
    }
  };

  const createSessions = () => {
    const totalSessions =
      sessions && sessions[sessions.length - 1] && sessions[1].total_sessions;
    const sessionsNumber = [];
    for (let index = 0; index < totalSessions; index++) {
      sessionsNumber.push(
        <span
          key={index + 1}
          className="session"
          style={{ backgroundImage: `url(${getSessionImage(index + 1)})` }}
          onClick={() => onSessionClick(index + 1)}
        >
          <span
            style={{
              margin: "0 0 8px 16px",
              color: "white",
              backgroundColor: "#212121c9",
              border: "1px solid #212121c9",
              borderRadius: "25px",
              padding: "0 12px",
            }}
          >
            {`Sesión ${index + 1}`}
          </span>
        </span>
      );
    }
    return sessionsNumber;
  };

  return (
    <div style={{ color: "white", position: "relative" }}>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          marginBottom: "16px",
        }}
      >
        <img
          style={{ marginBottom: "16px", alignSelf: "center" }}
          src={tna}
          alt="TNA"
          onClick={() => history.push("/menu")}
        />
        <i
          onClick={() => history.push("/menu")}
          className="fa fa-chevron-left"
          aria-hidden="true"
          style={{ position: "absolute", fontSize: "18px", top: "15px" }}
        ></i>
        <div style={{ alignSelf: "flex-end", fontSize: "20px" }}>
          Sesiones por día
        </div>
      </div>
      {loading ? (
        <Loader
          style={{ textAlign: "center" }}
          type="MutatingDots"
          color="white"
          height={100}
          width={100}
        />
      ) : (
        createSessions()
      )}
    </div>
  );
};

export default Sessions;
