import React from "react";
import Dialog from "@material-ui/core/Dialog";
import Timer from "react-compound-timer";
import "../styles/restTimer.scss";

const RestTimer = (props) => {
  const { openTimer, setOpenTimer, restTime } = props;

  const handleClose = () => {
    setOpenTimer(false);
  };

  return (
    <div>
      <Dialog
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={openTimer}
        disableBackdropClick={true}
      >
        <div
          style={{
            padding: "2.5rem",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <span style={{ marginBottom: "2rem", fontSize: "19px" }}>
            Tiempo de descanso:
          </span>
          <Timer
            initialTime={restTime * 1000}
            direction={"backward"}
            timeToUpdate={0}
            checkpoints={[
              {
                time: 0,
                callback: () => handleClose(),
              },
            ]}
          >
            <span className="restTimer">
              <Timer.Minutes />:<Timer.Seconds />
            </span>
          </Timer>
        </div>
        <span
          onClick={handleClose}
          style={{
            color: "#212529",
            textAlign: "center",
            marginBottom: "1rem",
            fontWeight: "600",
          }}
        >
          Omitir descanso
        </span>
      </Dialog>
    </div>
  );
};

export default RestTimer;
