import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import YouTube from "react-youtube";
import axios from "axios";
import Swal from "sweetalert2";
import Loader from "react-loader-spinner";
import API_URL from "./api_constants/apiUrl";
import { HEADERS_WITH_TOKEN } from "./api_constants/headers";
import tna from "../img/TNA.png";

const ExerciseDetails = () => {
  const history = useHistory();
  const [exercise, setExercise] = useState([]);
  const [loading, setLoading] = useState(false);

  const headers = HEADERS_WITH_TOKEN;

  const getUserExercises = async () => {
    setLoading(true);
    try {
      const result = await axios.get(`${API_URL}/training/exercises/`, {
        headers,
      });
      setLoading(false);
      result.data.forEach((exercise) => {
        if (exercise.id.toString() === localStorage.getItem("exercise_id")) {
          console.log("Exercise", exercise);
          setExercise(exercise);
        }
      });
    } catch (e) {
      const error = e.response.data;
      const message = error[Object.keys(error)[0]];
      errorPopup(message);
      setLoading(false);
    }
  };

  const errorPopup = (message) => {
    Swal.fire({
      icon: "error",
      title: "Oops!...",
      text: message,
    });
  };

  useEffect(() => {
    getUserExercises();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  const opts = {
    width: "100%",
  };

  const getVideo = () => {
    return exercise && exercise.video_url && exercise.video_url.split("v=")[1];
  };

  return (
    <>
      {loading ? (
        <Loader
          style={{ textAlign: "center" }}
          type="MutatingDots"
          color="white"
          height={100}
          width={100}
        />
      ) : (
        <div style={{ color: "white", position: "relative" }}>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              marginBottom: "16px",
            }}
          >
            <img
              style={{ marginBottom: "16px", alignSelf: "center" }}
              src={tna}
              alt="TNA"
              onClick={() => history.push("/menu")}
            />
            <i
              onClick={() => history.push("/ejercicios")}
              className="fa fa-chevron-left"
              aria-hidden="true"
              style={{ position: "absolute", fontSize: "18px", top: "15px" }}
            ></i>
            <div
              style={{
                backgroundColor: "#c3ea4d",
                border: "2px solid #f8f9fa",
                padding: "3px 20px",
                borderRadius: "50px",
                color: "#212529",
                marginLeft: "8px",
                textAlign: "center",
                alignSelf: "flex-end",
              }}
            >
              {exercise && exercise.name}
            </div>
          </div>
          <div style={{ display: "flex", flexDirection: "column" }}>
            <YouTube videoId={getVideo()} opts={opts} />
            <div style={{ marginBottom: "5px" }}>
              <i
                className="fa fa-circle"
                style={{
                  fontSize: "8px",
                  color: "#c3ea4d",
                  marginRight: "5px",
                }}
                aria-hidden="true"
              ></i>
              {`Descripción: ${
                (exercise && exercise.description !== "") ||
                (exercise && exercise.notes)
              }`}
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default ExerciseDetails;
