import React from "react";
import tna from "../img/TNA.png";
import workout from "../img/home/workout.jpg";
import nutrition from "../img/home/nutrition.jpg";
import recipies from "../img/home/recipies.jpeg";
import promos from "../img/home/promos.jpg";
import profile from "../img/home/profile.png";
import { useHistory } from "react-router-dom";
import "../styles/home.scss";
import Logout from "../img/svgs/Logout";

const Home = () => {
  const history = useHistory();

  const logout = () => {
    localStorage.removeItem("user_token");
    history.push("/login");
  };

  return (
    <div
      style={{ display: "flex", flexDirection: "column", alignItems: "center" }}
    >
      <img style={{ marginBottom: "16px" }} src={tna} alt="TNA" />
      <span onClick={logout}>
        <Logout
          styles={{
            fill: "#e0e0e0",
            position: "absolute",
            right: "16px",
            top: "16px",
            width: "20px",
            height: "36px",
          }}
        />
      </span>
      <span
        className="option"
        style={{ backgroundImage: `url(${workout})` }}
        onClick={() => history.push("/sesiones")}
      >
        <span style={{ backgroundColor: "#ffffff99" }} className="option-title">
          Plan de entrenamiento
        </span>
      </span>
      <div
        className="option"
        style={{ backgroundImage: `url(${nutrition})` }}
        onClick={() => history.push("/proximamente")}
      >
        <span style={{ backgroundColor: "#ffffffad" }} className="option-title">
          Plan de alimentación
        </span>
      </div>
      <div
        className="option"
        style={{ backgroundImage: `url(${recipies})` }}
        onClick={() => history.push("/proximamente")}
      >
        <span style={{ backgroundColor: "#ffffffcc" }} className="option-title">
          Recetario
        </span>
      </div>
      <div
        className="option"
        style={{ backgroundImage: `url(${promos})` }}
        onClick={() => history.push("/proximamente")}
      >
        <span
          style={{ backgroundColor: "#ffffffb3" }}
          className="option-title"
        >{`Promociones & Beneficios`}</span>
      </div>
      <div
        className="option"
        style={{ backgroundImage: `url(${profile})` }}
        onClick={() => history.push("/perfil")}
      >
        <span
          style={{ backgroundColor: "#ffffff8f" }}
          className="option-title"
        >{`Mi perfil`}</span>
      </div>
    </div>
  );
};

export default Home;
