import React, { useState } from "react";
import { makeStyles, createMuiTheme } from "@material-ui/core/styles";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import StepContent from "@material-ui/core/StepContent";
import Button from "@material-ui/core/Button";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import RestTimer from "./RestTimer";
import ThemeProvider from "@material-ui/core/styles/MuiThemeProvider";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    marginTop: "5px",
  },
  button: {
    marginTop: theme.spacing(1),
    marginRight: theme.spacing(1),
    backgroundColor: "#c3ea4d",
    fontSize: "12px",
    color: "#212529",
  },
  actionsContainer: {
    marginBottom: theme.spacing(2),
  },
  resetContainer: {
    padding: "0 0 0 24px",
    backgroundColor: "#212529",
    color: "white",
  },
}));

const theme = createMuiTheme({
  overrides: {
    MuiStepIcon: {
      root: {
        "&$active": {
          color: "#c3ea4d",
          fontSize: "35px",
          marginLeft: "-5px",
          "& $text": {
            fill: "#212529",
            fontWeight: "bold",
          },
        },
        "&$completed": {
          color: "#c3ea4d",
        },
      },
      text: {
        fill: "white",
      },
    },
    MuiStepLabel: {
      label: {
        color: "#8a8c8e",
        "&$active": {
          color: "white",
          fontSize: "16px",
        },
        "&$completed": {
          color: "#c3ea4d",
        },
      },
    },
  },
});

function getStepContent(step) {
  switch (step) {
    case step:
      return `Cuando termines, presiona "siguiente"`;
    default:
      return "Paso incorrecto";
  }
}

const RepsStepper = (props) => {
  const { reps, rest } = props;
  const classes = useStyles();
  const steps = reps;

  const [activeStep, setActiveStep] = useState(0);
  const [openTimer, setOpenTimer] = useState(false);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    handleClickOpenTimer();
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  const handleClickOpenTimer = () => {
    if (activeStep !== steps.length - 1) {
      setOpenTimer(true);
    }
  };

  const getRestTime = () => {
    return rest[activeStep - 1];
  };

  return (
    <div className={classes.root}>
      <RestTimer
        openTimer={openTimer}
        setOpenTimer={setOpenTimer}
        restTime={getRestTime()}
      />
      <ThemeProvider theme={theme}>
        <Stepper
          style={{ backgroundColor: "#212529" }}
          activeStep={activeStep}
          orientation="vertical"
        >
          {steps.map((label, index) => (
            <Step key={label}>
              <StepLabel>{`Haz ${label} repeticiones`}</StepLabel>
              <StepContent>
                <Typography style={{ color: "white", fontSize: "14px" }}>
                  {getStepContent(index)}
                </Typography>
                <div className={classes.actionsContainer}>
                  <div>
                    <Button
                      disabled={activeStep === 0}
                      onClick={handleBack}
                      className={classes.button}
                    >
                      Atrás
                    </Button>
                    <Button
                      variant="contained"
                      onClick={handleNext}
                      className={classes.button}
                    >
                      {activeStep === steps.length - 1
                        ? "Finalizar"
                        : "Siguiente"}
                    </Button>
                  </div>
                </div>
              </StepContent>
            </Step>
          ))}
        </Stepper>
      </ThemeProvider>
      {activeStep === steps.length && (
        <Paper square elevation={0} className={classes.resetContainer}>
          <Typography>Terminaste con este ejercicio!</Typography>
          <Button onClick={handleReset} className={classes.button}>
            Volver a comenzar
          </Button>
        </Paper>
      )}
    </div>
  );
};

export default RepsStepper;
