import React from "react";
import tna from "../img/TNA.png";
import { useHistory } from "react-router-dom";

const Soon = () => {
  const history = useHistory();

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        color: "white"
      }}
    >
      <div>
        <i
          style={{
            position: "absolute",
            left: "0",
            paddingLeft: "16px",
            top: "27px",
            fontSize: "18px"
          }}
          onClick={() => history.push("/menu")}
          className="fa fa-chevron-left"
          aria-hidden="true"
        ></i>
        <img style={{ marginBottom: "32px" }} src={tna} alt="TNA" />
      </div>
      <div style={{ fontSize: "30px" }}>¡Próximamente!</div>
    </div>
  );
};

export default Soon;
