import React from "react";
import { HashRouter, Route, Switch, Redirect } from "react-router-dom";
import Sessions from "./Sessions";
import "../styles/main.scss";
import Login from "./Login";
import Signup from "./Signup";
import UserProfile from "./UserProfile";
import Home from "./Home";
import Exercises from "./Exercises";
import ExerciseDetails from "./ExerciseDetails";
import Soon from "./Soon";

const Main = () => {
  if (localStorage.getItem("user_token") == null) {
    return <Redirect to="/login" />;
  } else {
    return (
      <HashRouter>
        <div className="contenedor">
          <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/menu" component={Home} />
            <Route path="/sesiones" component={Sessions} />
            <Route path="/ejercicios" component={Exercises} />
            <Route path="/explicacion-ejercicio" component={ExerciseDetails} />
            <Route path="/login" component={Login} />
            <Route path="/registrarse" component={Signup} />
            <Route path="/perfil" component={UserProfile} />
            <Route path="/proximamente" component={Soon} />
          </Switch>
        </div>
      </HashRouter>
    );
  }
};

export default Main;
